/*
pale gold: #6ABEDB
silk: #F0F0F0
paper: #f4f4f4
charcoal #373737

black: #201F1D
grey: #757571
light-grey: #B0ABA0
blue: #6ABEDB
white: #F0F0F0

*/

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
}

body {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  line-height: 1.35;
  background: #373737;
  color: #f4f4f4;
}

.main {
  min-height: calc(100vh - 200px);
}

footer {
  height: 150px;
}

.wrapper {
  min-height: 100vh;
}

h1,
h2 {
  color: #6ABEDB;
}

h3,
h4,
h5,
h6 {
  color: #201F1D;
}

a {
  color: #6ABEDB;
  text-decoration: none;
}

a:hover {
  color: #f4f4f4;
}

/* Skills Section */
.skill-box {
  padding: 1em;
  background: #F0F0F0;
}

ul.skill-grid {
  display: grid;
  clear: both;
  margin: 0px;
  padding: 0px;
  grid-template-columns: 300px 300px 300px 300px;
  grid-template-rows: auto;
  grid-column-gap: 10px;
  grid-row-gap: 15px;
}

ul.skill-grid li {
  list-style-type: none;
  display: block;
  float: left;
  margin: 1% 0 1% 2%;
}

/* Experience Page */
.timeline ul li {
  list-style-type: none;
  position: relative;
  width: 6px;
  margin: 0 auto;
  padding-top: 50px;
  background: #F0F0F0;
}

.timeline ul li::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: inherit;
}

.timeline ul li div {
  position: relative;
  bottom: 0;
  width: 400px;
  padding: 15px;
  background: #F0F0F0;
  border: 2px solid #6ABEDB;
}

.timeline ul li div h3,
.timeline ul li div h4 {
  color: #000;
  margin: 0;
  font-size: 1.2em;
  font-weight: bold;
}

.timeline ul li div::before {
  content: '';
  position: absolute;
  bottom: 7px;
  width: 0;
  height: 0;
  border-style: solid;
}

/* Odd elements */
.timeline ul li:nth-child(odd) div {
  left: 45px;
}

.timeline ul li:nth-child(odd) div::before {
  left: -15px;
  border-width: 8px 16px 8px 0;
  border-color: transparent #F0F0F0 transparent transparent;
}

/* Even elements */
.timeline ul li:nth-child(even) div {
  left: -439px;
}

.timeline ul li:nth-child(even) div::before {
  right: -15px;
  border-width: 8px 0 8px 16px;
  border-color: transparent transparent transparent #F0F0F0;
}

time {
  display: block;
  font-size: 1.3rem;
  font-weight: bold;
  margin-bottom: 5px;
}